// ============================================
// Sri Lankan Electricity Bill Configuration
// ============================================

// Configuration object: All pricing rules in one place for easy modification
const BILLING_CONFIG = {
  // Energy charge rates per unit (LKR/kWh) for each slab
  slabRates: [
    { min: 0,   max: 30,  rate: 8.0,  name: 'Slab A' },   // First 30 units: cheapest
    { min: 31,  max: 60,  rate: 15.0, name: 'Slab B' },   // Next 30 units: medium-low
    { min: 61,  max: 90,  rate: 20.0, name: 'Slab C' },   // Next 30 units: medium
    { min: 91,  max: 120, rate: 30.0, name: 'Slab D' },   // Next 30 units: medium-high
    { min: 121, max: Infinity, rate: 50.0, name: 'Slab E' } // Above 120: most expensive
  ],
  
  // Fixed charges (LKR) based on highest slab reached
  fixedCharges: {
    'Slab A': 100,   // Low consumption
    'Slab B': 250,   // Medium-low
    'Slab C': 400,   // Medium
    'Slab D': 600,   // Medium-high
    'Slab E': 1000   // High consumption
  },
  
  // Special rules
  lowConsumptionDiscount: 0.5,  // 50% off fixed charge if  60 units
  lowConsumptionThreshold: 60,  // Units threshold for discount
  highConsumptionPenalty: 500,  // Extra charge (LKR) if > 120 units
  highConsumptionThreshold: 120 // Units threshold for penalty
};

/**
 * Calculate Sri Lankan domestic electricity bill
 * 
 * @param unitsConsumed - Total kWh consumed in the month (must be >= 0)
 * @returns Object with bill breakdown and total
 * 
 * How it works:
 * 1. Count how many units fall in each price range (slab)
 * 2. Multiply units  rate for each slab
 * 3. Add them all up = energy charge
 * 4. Add fixed charge based on highest slab reached
 * 5. Apply discounts or penalties based on total consumption
 * 6. Return the final bill
 */
function calculateElectricityBill(unitsConsumed: number): {
  energyCharge: number;
  fixedCharge: number;
  discount: number;
  penalty: number;
  totalBill: number;
  breakdown: Array<{ slab: string; units: number; rate: number; charge: number }>;
  highestSlab: string;
} {
  // Safety check: Can't consume negative electricity!
  if (unitsConsumed < 0) {
    unitsConsumed = 0;
  }
  
  // Round to integer (you can't consume 0.5 kWh in billing)
  const units = Math.round(unitsConsumed);
  
  // Variables to track our calculation
  let energyCharge = 0;        // Total cost for the electricity used
  let highestSlab = 'Slab A';  // Track which slab we reached
  const breakdown: Array<{ slab: string; units: number; rate: number; charge: number }> = [];
  
  // Step 1: Calculate energy charge slab by slab
  // Think of it like climbing stairs - you pay different amounts per floor
  for (const slab of BILLING_CONFIG.slabRates) {
    if (units <= 0) break; // No more units to count
    
    // How many units fall in THIS slab?
    // Example: If you used 75 units and we're at slab C (61-90):
    // - You have 75 units left to count
    // - Slab C can hold up to 90 units
    // - So this slab gets min(75, 90) - 60 = 15 units
    const unitsInThisSlab = Math.min(units, slab.max) - Math.max(0, slab.min - 1);
    
    if (unitsInThisSlab > 0) {
      // Calculate charge for this slab: units  rate
      const slabCharge = unitsInThisSlab * slab.rate;
      energyCharge += slabCharge;
      highestSlab = slab.name; // Update highest slab reached
      
      // Save breakdown for detailed view
      breakdown.push({
        slab: slab.name,
        units: unitsInThisSlab,
        rate: slab.rate,
        charge: slabCharge
      });
    }
    
    // Stop if we've reached the end of this slab
    if (units <= slab.max) break;
  }
  
  // Step 2: Get fixed charge based on highest slab
  let fixedCharge = BILLING_CONFIG.fixedCharges[highestSlab as keyof typeof BILLING_CONFIG.fixedCharges];

  // Step 3: Apply 50% discount on fixed charge if consumption  60 units
  let discount = 0;
  if (units <= BILLING_CONFIG.lowConsumptionThreshold) {
    discount = fixedCharge * BILLING_CONFIG.lowConsumptionDiscount;
    fixedCharge = fixedCharge - discount; // Reduce fixed charge
  }
  
  // Step 4: Add penalty for high consumption (> 120 units)
  let penalty = 0;
  if (units > BILLING_CONFIG.highConsumptionThreshold) {
    penalty = BILLING_CONFIG.highConsumptionPenalty;
  }
  
  // Step 5: Calculate total bill
  const totalBill = Math.max(0, energyCharge + fixedCharge + penalty);
  
  return {
    energyCharge: Math.round(energyCharge * 100) / 100,      // Round to 2 decimals
    fixedCharge: Math.round(fixedCharge * 100) / 100,
    discount: Math.round(discount * 100) / 100,
    penalty,
    totalBill: Math.round(totalBill * 100) / 100,
    breakdown,
    highestSlab
  };
}

